:root {
  --main-font-family: "Mashetic", sans-serif;
  --secondary-font-family: "Poppins", sans-serif;
  --main-font-weight: 400;
  --accent-color: #f77307;
  --linear-gradient: linear-gradient(90deg, #3b34a1 0%, #e1400f 100%);
  --radial-gradient: radial-gradient(50% 50% at 50% 50%, #0c36b9b0 0%, #05185300 100%);
  --main-website-bacground-color: #001531;
  --secondary-website-bacground-color: #3b34a045;
  --primary-text-color-theme-dark: #fff;
}

html {
  scroll-behavior: smooth;
}

.list {
  list-style: none;
}

a:link {
  text-decoration: none;
}

body {
  background: var(--main-website-bacground-color);
  font-family: var(--main-font-family);
  font-weight: var(--main-font-weight);
  margin: 0;
}

button {
  cursor: pointer;
}

h1, h2, h3, h4, p {
  color: var(--primary-text-color-theme-dark);
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}

img {
  display: block;
}

.common-buttons {
  background: var(--linear-gradient);
  color: var(--primary-text-color-theme-dark);
  border: none;
}

.common-buttons:hover, .common-buttons:focus {
  border: 3px solid;
  border-image: var(--linear-gradient);
  background: none;
  border-image-slice: 1;
}

.circle {
  background: var(--accent-color);
  border-radius: 50%;
  position: absolute;
  top: -50px;
  left: 50%;
  transform: translateX(-50%);
}

.radial-gradient {
  background: var(--radial-gradient);
  z-index: -1;
  position: absolute;
  transform: translate(-50%, -50%);
}

.container {
  padding-left: 31px;
  padding-right: 31px;
